<!-- using Composition API -->

<template>
  <div style="height: 100px;">
    <span id="tip-1" class="tooltip-search-anchor">
      <span  anchor="tip-1" class="tooltip-search"> If a string match is typed a <a class="text-links"
          href=https://en.wikipedia.org/wiki/Trie>Trie</a>
        confirms it and the price is shown. 
        <br>Type <b>bread</b> to test it out.
        <br><i>This tooltip uses the new CSS <a class="text-links" href="https://www.w3.org/TR/css-anchor-position-1/">anchor</a> feature.</i>
      </span>
    </span>
    <input ref="inputField" type="text" v-model="searchTerm" placeholder="Search items..." /><span
      @click="printTrie"></span>
    <div v-for="item in filteredItems" :key="item.id">
      <div v-if="searchTerm.length > 0">
        {{ item.name }} <span v-if="trie.search(searchTerm.toLowerCase())"> {{ item.price }}</span>

      </div>
    </div>
  </div>
</template>

<script>
import { defineComponent } from 'vue';
import { ref, computed, onMounted } from 'vue';
import { productInventory } from '../Store/productInventoryOptionsData';
import { Trie } from './MakeTrie';

// focus directive to the email input
const focus = {
  mounted: (el) => el.focus()
}

export default {
  directives: {
    focus: {
      // directive definition
      mounted(el) {
        el.focus();
      },
    },
  },
  name: 'SearchFunction',
  setup() {
    
    const searchTerm = ref('');
    const items = ref(productInventory);
    const inputField = ref(null);
    
    let trie = new Trie;
    items.value.forEach(item => {
      trie.insert(item.name.toLowerCase());
    });
    
    
    // if someone is using a mobile device, the input field will not be focused!
    onMounted(() => {
      if (window.innerWidth > 600) {
        inputField.value.focus();
      }
    });
    
    const printTrie = () => {
      trie.print();
    };



    const filteredItems = computed(() => {
      if (!searchTerm.value) {
        return items.value;
      }

      return items.value.filter(item =>
        item.name.toLowerCase().includes(searchTerm.value.toLowerCase())
      );
    });

    return {
      searchTerm,
      items,
      filteredItems,
      trie,
      printTrie,
      inputField,

    };
  },
};
</script>

<style scoped>

input {
  font-size: 1.25em;
  border-radius: 2rem;
  border-width: 1px;
  outline: none;
  border: 1px solid rgb(218, 220, 224);
  padding: 0.25em;
}





/* tooltip-searchs on hover of anchors 

https://www.w3.org/TR/css-anchor-position-1/  https://www.w3.org/TR/css-anchor-position-1/

Uses new in 2023 Anchor CSS feature
*/
.tooltip-search-anchor {
  border: 2px solid rgb(255, 255, 255);
  padding: .45rem;

  margin-right: 1rem;
  border-radius: 0.5rem;
  cursor: help;
 
}
.tooltip-search-anchor:hover {  
  border: none;
}

.tooltip-search-anchor::after {
  content: "?";
  font-weight: bold;
}

.tooltip-search {
  line-height: 1.5;
  display: none;
  text-align: left;
  position: absolute;
  left: calc(anchor(implicit right) + .25em);
  bottom: calc(anchor(implicit top) + .25em);
}

.tooltip-search-anchor:hover>.tooltip-search {
  display: initial;
  color: rgb(18, 18, 18);
}

/* clickable tooltip-searchs using Popup API and anchor positioning API */
.tooltip-search,
[popover] {
  background: white;
  padding: .75rem;
 
  border-radius: 1rem;
  
  width: 21rem;
  box-shadow: 0 0 0.5rem rgba(0, 0, 0, 0.5);
  margin-left: -0.6rem;
}

.tooltip-search-anchor[popovertarget] {
  cursor: pointer;
}</style>
